/* style for each box in the grid */
body {
  font-family: 'M PLUS Rounded 1c', sans-serif;
}
.box {
display: inline-block;  
position: relative;
border: 1px solid black;
border-radius: 5px;
width: 15px;
height: 15px;
margin-left: -1px;
margin-bottom: -1px;
}

.off {
  background-color: #e2e2e2;
}

.on {
  background-color: #8ccc24;
}

.box:hover { 
  background: #00CCFF;
}

.center {
  margin: auto;
  display: table;
  margin-top: 10px;
}

.grid {
  width: 150px;
  line-height: 0;
  margin: auto;
  box-shadow: 0px 0px 20px white;
  margin-top: 20px;
}
  
h1, h2, h3, h4 {
  margin: auto;
  text-align: center;
  margin-top: 10px;
}

h4 {
  margin-top: 0px;
}

.description {
  margin-top: 50px;
  max-width: 700px;
  font-family: 'M PLUS Rounded 1c', sans-serif;
}
.description .game-rule {
  color: #f53e3e;
}
.App{
  font-family: 'M PLUS Rounded 1c', sans-serif;
}